import { hashPassword } from '@/shared/Hash'
import JsonViewer from '@/components/JsonViewer'
import { required, minValue } from 'vuelidate/lib/validators'
import VueNumeric from 'vue-numeric'
import config from './employee-create.config.json'
export default {
    name: 'employee-create',
    data() {
        return {
            config,
            employee: {
                name: '',
                nik: '',
                npwp: '',
                address: '',
                email: '',
                password: '',
                taxStatus: 'TK',
                phone: '',
                gender: 'm',
                bankNameP: '',
                accountNumberP: '',
                accountNameP: '',
                bankNameL: '',
                accountNumberL: '',
                accountNameL: '',
                role: null,
                loanLimit: 0,
                salary: 0,
                joinDate: ''
            },
            errorMessage: ''
        }
    },
    validations: {
        employee: {
            name: { required },
            nik: { required },
            npwp: { required },
            address: { required },
            email: { required },
            password: { required },
            taxStatus: { required },
            phone: { required },
            gender: { required },
            bankNameP: { required },
            accountNumberP: { required },
            accountNameP: { required },
            bankNameL: { required },
            accountNumberL: { required },
            accountNameL: { required },
            role: { required },
            loanLimit: { required, minValue: minValue(1) },
            salary: { required, minValue: minValue(1) },
            joinDate: { required }
        },
    },
    components: {
        JsonViewer,
        VueNumeric
    },
    methods: {
        handleSubmit() {
            this.$v.$touch()
            if (this.$v.$invalid) {
                this.errorMessage = 'Please complete mandatory fields'
            } else {
                this.$bvModal.show('modal-confirmation')
            }
        },
        handleModalConfirmationOk() {
            this.$bvModal.show('modal-loading')
            setTimeout(() => {
                this.$http({
                    method: 'post',
                    url: '/employee/managed-employee',
                    headers: {
                        Authorization: 'Bearer ' + this.$store.getters.getToken
                    },
                    data: {
                        name: this.employee.name,
                        address: this.employee.address,
                        nik: this.employee.nik,
                        npwp: this.employee.npwp,
                        tax_status: this.employee.taxStatus,
                        cell_phone: this.employee.phone,
                        gender: this.employee.gender,
                        email: this.employee.email,
                        user_name: this.employee.email,
                        user_pass: hashPassword(this.employee.email, this.employee.password).toString(),
                        role_name: this.employee.role,
                        loan_limit: this.employee.loanLimit,
                        salary: this.employee.salary,
                        join_date: this.employee.joinDate,
                        bank_name_p: this.employee.bankNameP,
                        account_number_p: this.employee.accountNumberP,
                        account_name_p: this.employee.accountNameP,
                        bank_name_l: this.employee.bankNameL,
                        account_number_l: this.employee.accountNumberL,
                        account_name_l: this.employee.accountNameL,
                        id_employee_admin: this.$store.getters.getUid
                    }
                })
                .then(response => {
                    this.$bvModal.hide('modal-loading')
                    response.data.response_code == '01' ? this.$router.push('/employee') : this.errorMessage = response.data.response
                })
                .catch(error => {
                    this.$bvModal.hide('modal-loading')
                    this.errorMessage = error.response.data.message
                })
            }, 500)
        },
        resetErrorMessage() {
            this.errorMessage = ''
        }
    }
}